<script>
export default {
    name: "Pokemons",
    mounted() {
        let currenteAnimation = 0;
        const animations = document.querySelectorAll('.animation');

        function showNextAnimation() {
            animations[currenteAnimation].classList.remove('active');

            currenteAnimation = (currenteAnimation + 1) % animations.length;
            setTimeout(() => {
                animations[currenteAnimation].classList.add('active');
            }, 500)
        }

        animations[currenteAnimation].classList.add('active');

        setInterval(showNextAnimation, 5000);
    }
}
</script>
